	display:block;
	cursor:pointer;
}
.cta:visited {
	text-decoration: none;
	color: white;
}
.cta:hover {
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
