}
.card .card-photo {
	font-size: 26px;
	font-weight: 500;
	padding: 30px 15px;
	-webkit-animation: slide 30s linear infinite;
}
.card .card-photo.inverted {
	color:white;
}
.card .card-text {
	padding:0px 15px;
text-align:left;
}
.card .card-cta-container {
	padding:10px;
