	color:#0D47A1;
	font-weight:500;
}
.message-box.show {
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
}
.message-box .column {
	width:50%;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
